gsap.registerPlugin(ScrollTrigger);

let tl = gsap.timeline();

tl.from('.header__logo', {
   yPercent: -200
  })
  .from('.nav__item', {
   stagger: 0.1,
   yPercent: -300
  })
  .to('.theme', {
    y: 0,
    opacity: 1,
    scale: 1,
    ease: 'power1.in'
   })
  .fromTo('.hero__title', {
    y: 50,
    opacity: 0
    },
    {
     y: 0,
     opacity: 1,
     ease: "sine.out"})
  .fromTo('.hero__subtitle', {
    y: 50,
    opacity: 0
   },
   {
    y: 0,
    opacity: 1,
    ease: "sine.out"
  })
  .fromTo('.hero__btn', {
    y: 50,
    opacity: 0
   },
   {
    y: 0,
    opacity: 1,
    ease: "sine.out"
  })
  .fromTo('.hero__image > img', {
    opacity: 0,
    scale: 0
  },
  {
    opacity: 1,
    scale: 1,
    ease: "sine.in" })
  .fromTo('.hero__tooltip', {
    opacity: 0
   },
   {
    opacity: 1,
    stagger: 0.25});


gsap.from('.services__title', {
  scrollTrigger: {
    trigger: '.services__title',
    start: 'top 95%',
    toggleActions: "play none none none"
  },
  ease: "slow.inOut",
  y: 100,
})

